<template>
  <v-btn
      class="mx-2"
      fab
      dark
      left
      samll
      absolute
      color="rgba(255, 255, 255, 0.3)"
      id="pageUpBtn"
      @click="toTop()"
  >
    <v-icon dark>
      mdi-arrow-up
    </v-icon>
  </v-btn>
</template>

<script>
export default {
  name: 'PageUpBtn',

  data() {
    return {
    };
  },
  mounted() {
    window.addEventListener('scroll', this.handleScroll);
  },
  methods: {
    toTop: function () {
      window.scrollTo({
        top: 0,
        behavior: "smooth"
      });
    },
  }
}
</script>

<style>
#pageUpBtn {
  bottom: 0;
  margin: 0 0 16px 16px;
  position: fixed;
}
</style>