import Vue from 'vue';
import VueRouter from 'vue-router';
import PageHome from '@/views/PageHome.vue'

Vue.use(VueRouter);

const routes = [
    {
        path: '/',
        name: 'PageHome',
        component: PageHome
    },
    {
        path: '/about',
        name: 'PageAbout',
        component: () => import('../views/PageAbout.vue')
    },
    {
        path: '/qatar',
        name: 'PageQatar',
        component: () => import('../views/PageQatar.vue')
    },
    {
        path: '/qatar/qpre',
        name: 'PageQatarPre',
        component: () => import('../views/PageQatarPre.vue')
    }
];

const router = new VueRouter({
    mode: 'history',
    routes,
});

export default router;